import React from 'react'
import './Movie.css'
import image from './image.jpg'
import { API_URL, API_KEY, IMAGE_URL, EXTEND_SIZE, YOUTUBE_URL, DEFAULT_TRAILER} from '..//config'
import {setLocalStorage, getLocalStorage} from '../localStorage/localStorage'
import Loader from '../Loader/Loader'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'


class Movie extends React.Component {
    state = {
        results: [],
        trailer: "",
        badges: [
            "success",
            "warning",
            "danger",
            "primary",
            "info",
            "secondary",
            "dark"
        ],
        movieBadges: []
    }

    getStateResults() {
        return this.state.results ? true : false
    }

    async componentDidMount(){
        const movieId = this.props.match.params.movie
        const movieLocalState = getLocalStorage(movieId)
        if(movieLocalState) {
            this.setState({
                results: movieLocalState.results,
                trailer: movieLocalState.trailer
            }, this.setBadges)
        } else {
            const urlData = `${API_URL}movie/${movieId}?api_key=${API_KEY}`
            
            const fetchInformation = await fetch(urlData);
            const results = await fetchInformation.json()

            const fetchTrailer = await fetch(YOUTUBE_URL(movieId));
            const resultsTrailer = await fetchTrailer.json()
            
            const trailer = resultsTrailer.results[0]
            ? resultsTrailer.results[0].key : DEFAULT_TRAILER;

            setLocalStorage(movieId, {results, trailer})
            this.setState({
                results,
                trailer
            }, this.setBadges)
        }
        
        //console.log(this.state.movieBadges)
    }

    setBadges() {
        let badgesId = [];
        if (!this.state.results.genres) return "loading.."
        for (let i = 0; i < this.state.results.genres.length; i++) {
            let currentBadge = Math.round(Math.random() * 6)
            badgesId.some(badge => currentBadge === badge)
                ? i--
                : badgesId.push(currentBadge)
        }
        
        const badges = this.state.results.genres.map((genre, id) => (
                <Draggable draggableId={id + genre.name} index={id} key={id}>
                    {(provided)=>(
                        <span
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            ref={provided.innerRef}
                            key={id}
                            className={`badge badge-pill badge-${this.state.badges[badgesId[id]]}`}
                            >
                            {genre.name}
                        </span>  
                    )}
                </Draggable>
            ))
        this.setState({movieBadges: badges})
    }

    averageRate() {
        return Math.round(this.state.results.vote_average);
    }

    setRate() {
        const movieRate = this.averageRate() || 0;
        const stars = [];
        for(let i = 0; i < 10; i++){
            stars[i] = (
                <span 
                key={i}
                className={`fa fa-star${i<movieRate? " checked" : ""}`}>
                </span>
            )
        }
        return stars
    }

    onDragEnd = ({destination, source})=> {
        if(!destination) return;
        if(destination.index === source.index) return;
        console.log(this.state.movieBadges)
        const newStateBadges = [...this.state.movieBadges]
        const replacedItemFrom = newStateBadges[source.index]
        newStateBadges.splice(source.index, 1)
        newStateBadges.splice(destination.index, 0, replacedItemFrom)
        this.setState({movieBadges:newStateBadges})
    }


    render = () => {
        //console.log(this.state.movieBadges)
        return (
            <div className="container row new-row">
                {
                    this.state.trailer
                    ?
                        <img src={ this.state.results.poster_path 
                        ? `${IMAGE_URL}${EXTEND_SIZE}${this.state.results.poster_path}` 
                        : image} className="rounded col-xl-4 offset-md-1" alt="movie-post" />
                    : <Loader/>
                }
                
                <div className="col-xl-4 offset-xl-1">
                    <h1>{this.state.results.title}</h1>
                    <hr />
                    <strong> Description: </strong>
                    <p>
                        { this.state.results.overview || "loading.."}
                    </p>
                    <hr />
                    <DragDropContext onDragEnd={this.onDragEnd}>
                        <div >
                            <strong>Generes: </strong>
                            <Droppable droppableId="droppable" direction="horizontal">
                                { (provided)=> ( 
                                    <span
                                        ref={provided.innerRef}
                                        {...provided.droppableProps}
                                    >
                                        {this.state.movieBadges}
                                        {provided.placeholder}
                                    </span>
                                )} 
                                
                            </Droppable>

                        </div>
                    </DragDropContext>


                    <hr />

                    <div><strong>Rate: </strong><i>{this.averageRate() || "?"}/10</i></div>
                    {this.setRate()}
                    <hr />
                    <div>
                        <strong> Trailer: </strong>
                    </div>
                    <div className="trailer">
                        {
                            this.state.trailer 
                            ? (<iframe frameBorder="0" allowFullScreen="1"
                                    title="YouTube video player"
                                    src={`https://www.youtube.com/embed/${this.state.trailer}?controls=1`}/>)
                            : "loading..."
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default Movie

