import React from 'react'
import Content from '..//Content/Content'
import './Popular.css'
import { POPULAR_URL, POPULAR } from '../config'
import { getLocalStorage } from '../localStorage/localStorage'
import Loader from '../Loader/Loader'
import ArrowTop from '../ArrowTop/ArrowTop'
import { useState, useEffect } from 'react'
import {setLocalStorage} from '../localStorage/localStorage'

function Popular() {
    const [results, setResults] = useState([])
    const [page, setPage] = useState(1)
    const [loading, setLoading] = useState(true)

    const handlerLoading = async () => {
        const fetched = await fetch(POPULAR_URL(page));
        const data = await fetched.json()
        const state = {
            results: [...results, ...data.results],
            page: page + 1,
            loading: false
        }
        setLocalStorage(POPULAR, state)

        setResults([...results, ...data.results])
        setPage(page + 1)
        setLoading(false)
    }

    useEffect(() => {
        if (getLocalStorage(POPULAR)) {
            const localState = getLocalStorage(POPULAR)
            setResults(localState.results)
            setPage(localState.page)
            setLoading(false)
        } else {
            handlerLoading()
        }
    }, [setResults, setPage, setLoading])

    return (
        <>
            <h1 className="display-1 mx-auto">Popular</h1>
            {
                loading
                    ? <Loader />
                    : <><div className="container-fluid">
                        <div className="card-deck row justify-content-center my-5">
                            <Content
                                results={results}
                                path="/"
                            />
                        </div>
                    </div>
                        <div>
                            <ArrowTop />
                            <button
                                className="btn btn-info mx-auto center"
                                onClick={handlerLoading}>Load more..</button>
                        </div>

                    </>
            }
        </>
    )
}


export default Popular;